import { Component, OnInit, OnChanges, Input } from "@angular/core";
import { Bar } from "../bar.model";

const DEFAULT_PADDING: number = 2;

@Component({
  selector: "statistics-list-bar",
  template: `<div class="statistics-container">
    <statistics-bar 
        class="statistics-item" 
        *ngFor="let bar of bars"  
        [width]="bar.width" 
        [bgColor]="bgColor" 
        [color]="color" 
        [barWidth]="barWidth"
        [ngStyle]="{'padding': padding+'px'}"
        ></statistics-bar>
    <div class="figures" *ngIf="showFigures">{{percentage}}%</div>
</div>
`,
  styles: [`.statistics-container{display:flex;flex-wrap:wrap}.statistics-item{margin-top:1px;padding:2px}.figures{margin-top:-4px;margin-left:4px}`]
})
export class StatisticsListBarComponent implements OnChanges, OnInit {
  @Input() bars: Bar[] = [];
  @Input() showFigures: boolean = false;
  @Input() percentage: number = 0;
  @Input() bgColor: string;
  @Input() color: string;
  @Input() barWidth: number;
  @Input() full: boolean = false;
  padding: number = DEFAULT_PADDING;

  constructor() {}

  ngOnInit() {
    if (this.full == true) {
      this.padding = 0;
    } else {
      this.padding = DEFAULT_PADDING;
    }
  }

  ngOnChanges() {}
}
